@import url("https://fonts.googleapis.com/css2?family=Forum:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Notable:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Comic+Neue:wght@400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-comic-neue: "Comic Neue";
  --font-notable: Notable;
  --font-forum: Forum;

  /* font sizes */
  --font-size-5xl: 24px;
  --font-size-45xl: 64px;

  /* Colors */
  --color-black: #000;
  --color-rosybrown: #926f72;

  /* Border radiuses */
  --br-61xl: 80px;
}
